import { h } from 'preact'
/* eslint-env browser */
import { Component, render } from 'preact'
// import { test } from '/update.js'
import LinkedIn from './LinkedIn.jsx'
import User from './User.jsx'
import callbackFetch from './../fetch.js'

class App extends Component {
  constructor() {
    super()
    this.state = {
      loading: true,
      error: null,
      auth: {},
    }
    this.pml = /** @type {function(!Event)} */(this.postMessageListener.bind(this))

    window.addEventListener('message', this.pml, false)
  }
  componentDidMount() {
    this.auth()
  }
  auth() {
    this.setState({ loading: true })
    callbackFetch(`${this.props.host}/user`, (error, res) => {
      this.setState({ loading: false })
      if (error) {
        return this.setState({ error })
      }
      const auth = res.json()
      this.setState({ auth })
    }, {
      credentials: 'include',
    })
  }
  /**
   * @param {!MessageEvent} event
   */
  postMessageListener(event) {
    const { data, origin } = event
    if (origin != this.props.host) return
    if (data == 'linkedin-signedin') this.auth()
  }
  componentWillUnmount() {
    window.removeEventListener('message', this.pml)
  }
  render() {
    if (this.state.error)
      return (h('div',{},`Error: `,this.state.error))
    if (this.state.loading)
      return (h('div',{},`Loading...`))
    if (!this.state.auth.user)
      return (h('div',{},
        h(LinkedIn,{host:this.props.host}),
      ))
    return (  h('div',{},
      h(User,{...this.state.auth,onSignout:() => {
        this.setState({ auth: {} })
      },host:this.props.host}),
    ))
  }
}

